import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, onActivated, onDeactivated, onMounted, ref } from 'vue';
export default defineComponent({
    name: 'CustomerMessage',
    setup() {
        const activeName = ref('sys');
        const sysMessageList = ref([]);
        const shopMessageList = ref([]);
        const getSysMessageList = () => {
            http.post('/hbSeller/fans/msg/system', {}, false).then((res) => {
                if (res.code === '200') {
                    sysMessageList.value = res.data;
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        const getShopMessageList = () => {
            http.post('/hbSeller/fans/msg/shop/query', {}, false).then((res) => {
                if (res.code === '200') {
                    shopMessageList.value = res.data;
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        onMounted(() => {
            getSysMessageList();
            getShopMessageList();
        });
        onActivated(() => {
            console.log('Activated!');
        });
        onDeactivated(() => {
            console.log('Deactivated');
        });
        return {
            activeName,
            sysMessageList,
            shopMessageList
        };
    }
});
